@import url(https://fonts.googleapis.com/css?family=Allerta+Stencil|Aref+Ruqaa);
@import url(https://fonts.googleapis.com/css?family=Allerta+Stencil|Aref+Ruqaa);
@import url(https://fonts.googleapis.com/css?family=Allerta+Stencil|Aref+Ruqaa);

.gameContainer {
  position: relative;
}

.boardContainer {
  width: 55%;
  margin: 55px auto 10px auto;
  padding: 5px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-column-gap: 5px;
  grid-row-gap: 5px;
}

.checkPointButtons {
  margin: 0px auto;
  text-align: center;
}

.checkpointButtons span, .checkpointButtons button {
  width: 55%;
  height: 45px;
  font-size: 25px;
  border: none;
  background-color: #000;
  color: white;
  font-family: 'Aref Ruqaa', serif;
}

.checkpointButtons button:hover {
  color: red;
  border-radius: 5px;
}

.squareContainer {
  height: 110px;
  text-align: center;
  cursor: pointer;
  font-size: 40px;
  font-family: 'Aref Ruqaa', serif;
  color: black;
  -webkit-animation: bounceIn 1s;
          animation: bounceIn 1s;
}

.valueHolder {
  margin: 15px auto;
}

@-webkit-keyframes bounceIn {
  0% {
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes bounceIn {
  0% {
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.squareContainer:hover {
  -webkit-transform: scale(0.96);
          transform: scale(0.96);
  transition: -webkit-transform 0.1s ease-in-out;
  transition: transform 0.1s ease-in-out;
  transition: transform 0.1s ease-in-out, -webkit-transform 0.1s ease-in-out;
}

.modalWindowBG {
  width: 55%;
  height: 91%;
  background-color: #00a8b5;
  position: absolute;
  top: 0.8%;
  left: 22.5%;
  font-family: 'Aref Ruqaa', serif;
}

.modalWindowContent {
  width: 99.5%;
  height: 99.5%;
  border: 2px solid white;
}

.content {
  width: 99%;
  height: 45%;
  margin: 26% auto;
  border: 2px solid black;
}

.content p {
  width: 100%;
  margin: 2% auto;
  font-size: 35px;
  text-align: center;
}

.content button {
  width: 40%;
  height: 20%;
  margin: 3% 29%;
  text-align: center;
  font-size: 30px;
  background-color: black;
  color: white;
  font-family: 'Aref Ruqaa', serif;
  border: none
}

.content button:hover {
  background-color: orangered;
  color: black;
}

